/* jshint ignore:start */

define('ember-stereophoto/config/environment', ['ember'], function(Ember) {
  var prefix = 'ember-stereophoto';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("ember-stereophoto/tests/test-helper");
} else {
  require("ember-stereophoto/app")["default"].create({"name":"ember-stereophoto","version":"0.0.0+"});
}

/* jshint ignore:end */
